.c-performance_background {
  background: #000000 url(../img/p1.jpg) no-repeat;
  background-size: cover;
  min-height: 475px;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 80%;
    background-image: linear-gradient(to top, #090909 0%, rgba(9, 9, 9, 0) 100%);
  }
}

.c-performance-title {
  position: relative;
}

.c-performance-title__name {
  font-size: 65px;
  line-height: 60px;
  color: #ffffff;
  font-family: 'AmericaXIX', serif;
  padding-top: 300px;
}

.c-performance-title__producer {
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: 'FiraSans', sans-serif;
  padding-top: 20px;
}

.c-performance-title__bottom {
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  font-family: 'FiraSansBook', sans-serif;
  padding-top: 50px;
  padding-bottom: 20px;
  display: flex;
}

.c-performance-title__description {
  flex-grow: 1;
}

.c-performance__about {
  background: transparent url(../img/blot5.png) no-repeat right top 10px;
}

.c-performance-about__photo {
  float: right;
  margin-right: 25px;
}

.c-performance-about__photo img {
  height: 433px;
  width: 306px;
}

.c-performance-about__title {
  font-size: 45px;
  line-height: 80px;
  color: #000000;
  font-family: 'AmericaXIX', serif;
  padding: 42px 0 22px;
}

.c-performance-about__content {
  font-size: 16px;
  line-height: 20px;
  color: #555555;
  font-family: 'FiraSansBook', sans-serif;
  margin-right: 385px;
}

.c-performance-about__content p {
  padding: 0;
  margin: 0;
}

.c-performance-consist {
}

.c-performance-consist__title {
  font-size: 20px;
  line-height: 30px;
  color: #000000;
  font-family: 'AmericaXIX', serif;
  padding: 30px 0 20px;
  text-align: center;
}

.c-performance-consist__table {
  width: 100%;
}

.c-performance-consist__table td {
  font-size: 16px;
  line-height: 24px;
  color: #555555;
  font-family: 'FiraSansBook', sans-serif;
  padding: 0 7px 20px;
  vertical-align: top;
  text-align: center;
}

.c-performance-consist__table td:first-child {
  width: 50%;
  text-align: right;
}

.c-performance-consist__table td:last-child {
  width: 50%;
  text-align: left;
}

.c-performance-consist__table .c-performance-table__link {
  color: #e74e0f;
  text-decoration: none;
  border-bottom: 1px solid #e74e0f;
}

.c-performance-table__who {
  display: block;
  font-family: 'FiraSansLightItalic', sans-serif;
}

.c-performance-producer-title {
  padding: 35px 0;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
  font-family: 'AmericaXIX', serif;
  text-align: center;
}

.c-performance-producer-name {
  text-align: center;
  padding-bottom: 70px;
}

.c-performance-producer-link {
  text-decoration: none;
  color: #e74e0f;
}

.c-performance-producer-link:hover {
  border-bottom: 1px solid #e74e0f;
}
.c-performance_center-text {
  text-align: center;
}

.c-performance__all-photo {
  font-size: 16px;
  line-height: 20px;
  color: #555555;
  font-family: 'FiraSansBook', sans-serif;
  padding: 10px 60px 10px 20px;
  display: inline-block;
  margin-top: 44px;
  position: relative;
}

.c-performance__all-photo::before, .c-performance__all-photo::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  width: 19px;
  height: 19px;
  right: 17px;
}

.c-performance__all-photo::before {
  background-color: #025fdf;
}

.c-performance__all-photo::after {
  background-color: #ff0080;
  right: 37px;
}

.c-performance-honors__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  line-height: 20px;
  color: #818181;
  font-family: 'FiraSansBook', sans-serif;
  max-width: 720px;
}

.c-performance-honors__list li {
  padding-left: 11px;
  padding-bottom: 28px;
  position: relative;
}

.c-performance-honors__list li:last-child {
  padding-bottom: 0;
}

.c-performance-honors__list li::before {
  content: "";
  position: absolute;
  border-radius: 50%;
  width: 3px;
  height: 3px;
  background-color: #818181;
  left: 5px;
  top: 8px;
}

.c-performance-press blockquote {
  font-size: 16px;
  line-height: 26px;
  color: #555555;
  font-family: 'FiraSansBookItalic', sans-serif;
  margin: 0;
  margin-left: 145px;
  text-indent: -46px;
}

.c-performance-press blockquote::before,
.c-performance-press blockquote::after {
  position: relative;
  display: inline-block;
  font-size: 165px;
  line-height: 26px;
  color: #cdcdcd;
  font-family: 'AmericaXIX', serif;
  z-index: -1;
  vertical-align: bottom;
}

.c-performance-press blockquote::before {
  content: "„";
  top: -45px;
  left: 22px;
}

.c-performance-press blockquote::after {
  content: "“";
  right: -20px;
  bottom: -45px;
}

.c-performance-press cite {
  font-size: 16px;
  font-style: normal;
  line-height: 24px;
  color: #555555;
  font-family: 'AmericaXIX', serif;
  margin: 32px 0 70px;
  display: block;
}

.c-performance-press cite:last-child {
  margin-bottom: 0;
}
